.note {
  height: 250px;
  margin: 5px;
  -webkit-box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.25);
          box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.25);
  border-radius: 2% / 50%;
  position: absolute;
  width: 250px;
}

.note:hover {
  cursor: pointer;
}

.note p {
  font-family: 'Gloria Hallelujah', cursive;
  font-size: 18px;
  margin: 0;
}

.front-note {
  background: -webkit-gradient(linear, left top, left bottom, from(#fefcaf), to(#fffeda));
  background: -webkit-linear-gradient(#fefcaf, #fffeda);
  background: -o-linear-gradient(#fefcaf, #fffeda);
  background: linear-gradient(#fefcaf, #fffeda);
  width: 100%;
  height: 100%;
  padding: 5px 20px 10px;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  z-index: 2;
}

.delete-sticky{
  font-family: 'Gloria Hallelujah', cursive;
  position: absolute;
  right: 10px;
  top: 0;
}



